import {  Grid } from '@material-ui/core'
import Form from './List/Form'
import { makeStyles} from '@material-ui/core/styles'
import { useState } from 'react'
import List from './List/List'


const useStyle = makeStyles((theme) => ({
  leftGridContainer: {
    paddingTop: theme.spacing(2),
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'flex-start'
  },
  rightGridContainer: {
    display: 'flex',
    flexDirection: 'column',
  },
  list: {
    display: 'flex', 
    flexDirection: 'column',
    alignItems: 'center'
  }
}))

const App = () => {
  const [habit, setHabit] = useState([])
  const classes = useStyle()
  

  const addHabit = (userInput) => {
    if(userInput) {
      const newList = {
        id: Math.random(),
        text: userInput,
        complete: false
      }
      setHabit([...habit, newList])
      console.log(habit);
      console.log(newList)
    }
    
  }
  const removeHabit = (id) => {
    setHabit([...habit.filter(habit => 
      habit.id !== id
      )])
  }
  const handleToggle = (id) => {
    setHabit([...habit.map(habit => 
      habit.id == id ? {...habit, complete: !habit.complete} : {...habit}
      )])
  }

  return (
    <Grid container>
      <Grid item md={3} className={classes.leftGridContainer}>
        <Form 
        addHabit= {addHabit}
        />
      </Grid>
      <Grid item md={9} className={classes.rightGridContainer}>
        <div className={classes.list}>
          <h3>Active habits: {habit.length}</h3>
          {habit.map((habit) => {
            return (
              <List key={habit.id}
              habit= {habit}
              toggleHabit = {handleToggle}
              removeHabit = {removeHabit}
              />
            )
          })}
        </div>
      </Grid>
    </Grid>
  )
}

export default App