
import ClearIcon  from '@material-ui/icons/Clear';
import CheckIcon from '@material-ui/icons/Check';


const List = ({ habit, toggleHabit, removeHabit }) => {
  return(
<div key={habit.id} className={habit.complete ? 'habit-done' : 'habit'}>
      {habit.text}
      <div className='habit-btn'>
        <CheckIcon onClick={() => toggleHabit(habit.id)}/>
        <ClearIcon onClick={() => removeHabit(habit.id)}/>
      </div>
    </div>
  )
}

export default List